<template>
  <button :style="border === undefined && 'border:1px solid #232323;' || border && 'border:1px solid #232323;'" :class="detectDisabled()">{{value}}</button>
</template>

<script>
export default {
  name: "Button",
  props:["value", "border", "fill", "disabled"],
  methods: {
    detectDisabled(){
      let classes = ''
      if(this.disabled){
        classes = 'disabled'
      }
      return classes;
    }
  }
}
</script>

<style scoped>
button{
  border: 0px;
  transition: 300ms;
  background: #232323;
  border-radius: 3px;
  cursor: pointer;
  color:#ffffff;
}
button:hover{
  box-shadow: 0px 0px 2px 3px rgba(35, 35, 35, 0.5);
}

.disabled{
  opacity: 65%;
  cursor: not-allowed
}

.disabled:hover{
  box-shadow: 0px 0px 0px 0px rgba(0, 138, 255, 0.5);
}
</style>